<template>
  <div>
    <script :id="id" type="text/plain" style="height:200px;"></script>
  </div>
</template>

<script>
  import vm from '../Vue.js'
  const UE = require('UE');
  export default {
    name: 'ueditor',

    data(){
      return {
        editor: null,
        id: "editor_" + Date.parse(new Date()) + this.type,
        config: {
          toolbars: [
            [
              'source',
              'fontfamily', //字体
              'fontsize', //字号
              'forecolor', //字体颜色
              'backcolor', //背景色
              'bold', //加粗
              'italic', //斜体
              'simpleupload', //单图上传
              'insertimage', //多图上传
              'justifyleft', //居左对齐
              'justifyright', //居右对齐
              'justifycenter', //居中对齐
              'justifyjustify', //两端对齐
              'undo',
              'redo',

            ]
          ],
        }
      }
    },
    props: ['content', 'type'],
    mounted() {
      var _this = this;
      this.editor = UE.getEditor(this.id, this.config);
      this.editor.addListener('blur', function (editor) {
        _this.$emit('getcontent', { content: _this.editor.getContent(), type: _this.type });
      });
      this.editor.addListener("ready", function () {
        _this.editor.setContent(_this.content);
      });

      vm.$on('setcontent', function (data) {
        _this.editor.setContent(data);
      })

    },
    destroyed() {

    },
  };
</script>



