<template>
  <el-tabs v-model="activeName" v-loading="listLoading"  @tab-click="handleClick">
    <el-tab-pane label="首页焦点图" name="first">
      <el-form ref="formBanner" v-model="banner" label-width="150px">
        <el-form-item label="焦点图片：">
          <el-upload
            class="upload-demo"
            action="/upload"
            :on-preview="handlePreview"
            :on-success = "handleSuccess"
            :before-upload = "beforeUpload"
            :file-list="banner.fileList"
            list-type="picture"
            ref="upload"
            style="width:400px;">
            <el-button type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="URL链接：">
          <el-input placeholder="请输入内容" v-model="banner.url" style="width:400px;">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="bannerSubmit" :loading="loading">提交</el-button>
        </el-form-item>
      </el-form>

    </el-tab-pane>
    <el-tab-pane label="加入我们" name="second">
      <div style="padding:10px 30px;">
        <ueditor :content="joinUs" :type="1"  @getcontent="getcontent"></ueditor>
        <el-button type="primary" @click="joinUsSubmit" :loading="loading" style="margin-top:10px;">提交</el-button>
      </div>
    </el-tab-pane>
    <el-tab-pane label="联系我们" name="third">
      <div style="padding:10px 30px;">
        <ueditor :content="contactUs" :type="2"  @getcontent="getcontent"></ueditor>
        <el-button type="primary" @click="contactUsSubmit" :loading="loading" style="margin-top:10px;">提交</el-button>
      </div>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
  import { editConfing ,getConfingList } from '../../api/api';
  import NProgress from 'nprogress'
  import Ueditor from '../../components/Ueditor.vue'
  import vm from '../../Vue.js'
  export default {
    data() {
      return {
        confing : [],
        loading : false,
        listLoading: false,
        activeName: 'first',
        banner : {
          fileList: [],
          url : ''
        },
        joinUs : '',
        contactUs : ''

      }
    },
    methods: {
      handlePreview(){

      },
      handleSuccess(response, file, fileList){
        let path =  response.path;
        let name = file.name;
        this.banner.fileList = [
          {
            name : name,
            url : path
          }
        ]
      },
      beforeUpload(){

      },
      bannerSubmit(){
        let para = {
          id : 1,
          content : JSON.stringify(this.banner)
        };
        this.editConfing(para)
      },
      joinUsSubmit(){
        let para = {
          id : 2,
          content : this.joinUs
        };
        this.editConfing(para)
      },
      contactUsSubmit(){
        let para = {
          id : 3,
          content : this.contactUs
        };
        this.editConfing(para)
      },
      editConfing(para){
        this.loading = true;
        editConfing(para).then((res) => {
          this.loading = false;
          NProgress.done();
          this.$notify({
            title: '成功',
            message: '提交成功',
            type: 'success'
          });
        });
      },
      getConfingList(){
        this.listLoading = true;
        NProgress.start();
        getConfingList({}).then((res) => {
          this.confing = res.data.data;
          this.listLoading = false;
          this.banner = JSON.parse(this.confing[0].content);
          this.joinUs = this.confing[1].content;
          this.contactUs = this.confing[2].content;
          NProgress.done();
        });
      },

      getcontent(data){
        if (data.type == 1) {
          this.joinUs = data.content;
        } else {
          this.contactUs = data.content;
        }
      },
      handleClick(tab, event){
        if(tab.name == 'second'){
          vm.$emit('setcontent', this.joinUs)
        }else if(tab.name == 'third'){
          vm.$emit('setcontent', this.contactUs)
        }
      }
    },
    components: {
      Ueditor
    },
    mounted() {
      this.getConfingList();
    }
  }

</script>
<style>
  .el-upload__tip {
    padding: 10px 0;
  }
  .el-tabs__nav-prev{
    display: none;
  }
  .el-tabs__nav-next{
    display: none;
  }
</style>



